@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@400;500;600;700&family=Inter:wght@300;400;500;600&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary: #10b981;
  --primary-dark: #059669;
  --secondary: #06b6d4;
  --accent: #8b5cf6;
  --bg-light: #fafafa;
  --bg-card-light: #ffffff;
  --text-light: #1f2937;
  --text-muted-light: #6b7280;
  --border-light: #e5e7eb;
}

.dark {
  --primary: #10b981;
  --primary-dark: #059669;
  --secondary: #06b6d4;
  --accent: #8b5cf6;
  --bg-light: #0f1419;
  --bg-card-light: #1a1f2e;
  --text-light: #f9fafb;
  --text-muted-light: #9ca3af;
  --border-light: #2d3748;
}

body {
  font-family: 'Inter', sans-serif;
  background: var(--bg-light);
  color: var(--text-light);
  min-height: 100vh;
}

h1, h2, h3 {
  font-family: 'Space Grotesk', sans-serif;
}

.landing-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #10b981 0%, #06b6d4 100%);
  position: relative;
  overflow: hidden;
}

.landing-page::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('https://images.unsplash.com/photo-1709846487437-7445553bb6ed?q=85') center/cover;
  opacity: 0.08;
  z-index: 0;
}

.theme-toggle {
  position: fixed;
  top: 2rem;
  right: 2rem;
  z-index: 100;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
}

.theme-toggle:hover {
  background: rgba(255, 255, 255, 0.25);
  transform: scale(1.05);
}

.hero-section {
  position: relative;
  z-index: 1;
  padding: 6rem 2rem 4rem;
  max-width: 1400px;
  margin: 0 auto;
}

.hero-content {
  text-align: center;
  margin-bottom: 5rem;
}

.hero-badge {
  display: inline-block;
  padding: 0.5rem 1.5rem;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 50px;
  color: white;
  font-size: 0.875rem;
  font-weight: 600;
  letter-spacing: 1px;
  margin-bottom: 2rem;
}

.hero-title {
  font-size: clamp(2.5rem, 6vw, 4.5rem);
  font-weight: 700;
  color: white;
  margin-bottom: 1.5rem;
  line-height: 1.1;
}

.hero-subtitle {
  font-size: clamp(1rem, 2vw, 1.25rem);
  color: rgba(255, 255, 255, 0.9);
  max-width: 600px;
  margin: 0 auto 3rem;
  line-height: 1.6;
}

.hero-actions {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
}

.get-started-btn {
  padding: 1rem 2.5rem;
  font-size: 1.125rem;
  font-weight: 600;
  background: white;
  color: #10b981;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.get-started-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
  background: #f9fafb;
}

.feature-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 4rem;
}

.feature-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 20px;
  padding: 2.5rem;
  text-align: center;
  transition: all 0.3s ease;
}

.feature-card:hover {
  background: rgba(255, 255, 255, 0.15);
  transform: translateY(-5px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.2);
}

.feature-icon {
  width: 48px;
  height: 48px;
  color: white;
  margin-bottom: 1.5rem;
}

.feature-card h3 {
  font-size: 1.5rem;
  color: white;
  margin-bottom: 1rem;
  font-weight: 600;
}

.feature-card p {
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.6;
}

.auth-modal {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}

.auth-content {
  background: var(--bg-card-light);
  border-radius: 24px;
  padding: 3rem;
  width: 100%;
  max-width: 460px;
  position: relative;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.3);
}

.modal-close {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  background: none;
  border: none;
  color: var(--text-muted-light);
  cursor: pointer;
  transition: color 0.2s;
}

.modal-close:hover {
  color: var(--text-light);
}

.auth-title {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  text-align: center;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: var(--text-light);
}

.auth-btn {
  width: 100%;
  padding: 0.875rem;
  font-size: 1rem;
  font-weight: 600;
  background: var(--primary);
  color: white;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
}

.auth-btn:hover {
  background: var(--primary-dark);
  transform: translateY(-1px);
}

.divider {
  text-align: center;
  margin: 1.5rem 0;
  color: var(--text-muted-light);
  position: relative;
}

.divider::before,
.divider::after {
  content: '';
  position: absolute;
  top: 50%;
  width: 40%;
  height: 1px;
  background: var(--border-light);
}

.divider::before {
  left: 0;
}

.divider::after {
  right: 0;
}

.google-btn {
  width: 100%;
  padding: 0.875rem;
  font-size: 1rem;
  font-weight: 600;
  background: white;
  color: #1f2937;
  border: 1px solid var(--border-light);
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
}

.google-btn:hover {
  background: #f9fafb;
  border-color: var(--primary);
}

.google-icon {
  width: 20px;
  height: 20px;
}

.auth-toggle {
  text-align: center;
  margin-top: 1.5rem;
  color: var(--text-muted-light);
}

.auth-toggle span {
  color: var(--primary);
  cursor: pointer;
  font-weight: 600;
}

.auth-toggle span:hover {
  text-decoration: underline;
}

.dashboard {
  min-height: 100vh;
  background: var(--bg-light);
}

.dashboard-nav {
  background: var(--bg-card-light);
  border-bottom: 1px solid var(--border-light);
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 50;
  backdrop-filter: blur(12px);
}

.nav-brand {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary);
}

.mobile-menu-btn {
  display: none;
  background: none;
  border: none;
  color: var(--text-light);
  cursor: pointer;
}

.nav-links {
  display: flex;
  gap: 0.5rem;
}

.nav-links button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.25rem;
  background: none;
  border: none;
  border-radius: 12px;
  color: var(--text-muted-light);
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.nav-links button:hover {
  background: var(--bg-light);
  color: var(--text-light);
}

.nav-links button.active {
  background: var(--primary);
  color: white;
}

.nav-actions {
  display: flex;
  gap: 0.75rem;
}

.icon-btn {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--bg-light);
  border: none;
  border-radius: 10px;
  color: var(--text-light);
  cursor: pointer;
  transition: all 0.2s;
}

.icon-btn:hover {
  background: var(--border-light);
}

.dashboard-content {
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
}

.section {
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.section-header h2 {
  font-size: 2rem;
  font-weight: 700;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 1.5rem;
}

.item-card {
  background: var(--bg-card-light);
  border: 1px solid var(--border-light);
  border-radius: 16px;
  padding: 1.5rem;
  transition: all 0.3s ease;
}

.item-card:hover {
  border-color: var(--primary);
  box-shadow: 0 8px 24px rgba(16, 185, 129, 0.12);
  transform: translateY(-2px);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.card-avatar {
  width: 60px;
  height: 60px;
  border-radius: 12px;
  background: linear-gradient(135deg, var(--primary), var(--secondary));
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  overflow: hidden;
}

.card-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card-actions {
  display: flex;
  gap: 0.5rem;
}

.card-actions button {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--bg-light);
  border: 1px solid var(--border-light);
  border-radius: 8px;
  color: var(--text-muted-light);
  cursor: pointer;
  transition: all 0.2s;
}

.card-actions button:hover {
  border-color: var(--primary);
  color: var(--primary);
}

.item-card h3 {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.item-card .email {
  color: var(--text-muted-light);
  font-size: 0.875rem;
  margin-bottom: 0.5rem;
}

.item-card .category {
  color: var(--text-muted-light);
  font-size: 0.875rem;
  margin-bottom: 0.5rem;
}

.item-card .bio {
  color: var(--text-muted-light);
  font-size: 0.875rem;
  line-height: 1.5;
  margin-bottom: 1rem;
}

.item-card .budget {
  font-size: 1.125rem;
  font-weight: 600;
  color: var(--primary);
  margin-top: 0.5rem;
}

.status-badge {
  display: inline-block;
  padding: 0.375rem 0.875rem;
  border-radius: 50px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: capitalize;
}

.status-badge.active {
  background: rgba(16, 185, 129, 0.1);
  color: var(--primary);
}

.status-badge.inactive,
.status-badge.paused {
  background: rgba(107, 114, 128, 0.1);
  color: var(--text-muted-light);
}

.status-badge.draft {
  background: rgba(251, 146, 60, 0.1);
  color: #f59e0b;
}

.status-badge.published {
  background: rgba(16, 185, 129, 0.1);
  color: var(--primary);
}

.status-badge.archived,
.status-badge.completed {
  background: rgba(139, 92, 246, 0.1);
  color: var(--accent);
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

@media (max-width: 768px) {
  .hero-section {
    padding: 4rem 1.5rem 3rem;
  }

  .feature-grid {
    grid-template-columns: 1fr;
  }

  .mobile-menu-btn {
    display: block;
  }

  .nav-links {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: var(--bg-card-light);
    border-bottom: 1px solid var(--border-light);
    flex-direction: column;
    padding: 1rem;
    display: none;
  }

  .nav-links.open {
    display: flex;
  }

  .grid {
    grid-template-columns: 1fr;
  }

  .dashboard-content {
    padding: 1.5rem;
  }

  .section-header {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
}